.nav {
  position: fixed;
  top: 0;
  width: 100%;
  max-height: 65px;
  z-index: 10;
}
.nav a, .nav button, .nav h3 {
  font-family: "Roboto Mono", monospace;
}
.nav svg {
  width: 28px;
  height: 28px;
}
.nav.show {
  background-color: rgba(0, 0, 0, 0.95);
}
.nav__navbar, .nav__web-menu-btn, .nav__menu-btn, .nav__menu-box, .nav__menu-logo, .nav__menu-items, .nav__menu-more-btn {
  display: flex;
}
.nav__navbar {
  position: relative;
  align-items: center;
  justify-content: center;
  height: 65px;
}
.nav__logo {
  position: absolute;
  display: none;
  left: 1em;
}
.nav__logo img {
  aspect-ratio: 3/1;
  width: 90px;
  height: 31px;
}
.nav__logo.show {
  display: block;
}
.nav__web-menu-btn {
  justify-content: flex-end;
  width: calc(100% + 26px);
  max-width: 995px;
}
.nav__menu-btn {
  padding: 0.3rem;
  background-color: transparent;
  color: rgb(255, 255, 255);
  font-size: 2.8rem;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.4s, color 0.4s;
}
.nav__menu-btn:hover {
  background-color: rgb(255, 255, 255);
  color: rgb(0, 0, 0);
}
.nav__menu {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100dvh;
  background-color: rgb(0, 0, 0);
  transform: translateX(-100%);
  opacity: 0;
  transition: transform 0.4s ease-in-out, opacity 0.4s;
}
.nav__menu-box {
  position: relative;
  justify-content: space-around;
  height: 60%;
}
.nav__menu-logo {
  margin-top: 10rem;
  justify-content: center;
  width: 20%;
}
.nav__menu-logo img {
  aspect-ratio: 11/4;
  width: 120px;
  height: auto;
}
.nav__menu-items {
  position: relative;
  top: 7rem;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  align-content: center;
  height: calc(100% - 70px);
  width: 50%;
  transition: transform 0.4s;
}
.nav__menu-items.hide {
  transform: translateX(150%);
}
.nav__menu-item {
  position: relative;
}
.nav__menu-item:last-of-type {
  pointer-events: none;
}
.nav__menu-item a,
.nav__menu-item button {
  display: block;
  font-size: 3.4rem;
  font-weight: 400;
  text-transform: uppercase;
  text-align: left;
  list-style: none;
  border: none;
  border-radius: 5px;
  background-color: transparent;
  transition: background-color 0.4s, color 0.4s;
  color: rgb(255, 255, 255);
  cursor: pointer;
}
.nav__menu-item a::after,
.nav__menu-item a a::before,
.nav__menu-item button::after,
.nav__menu-item button a::before {
  transition: 0.3s;
}
.nav__menu-item a::after,
.nav__menu-item button::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  height: 3px;
  width: 100%;
  background-color: rgb(196, 46, 56);
  transform: scalex(0);
  transform-origin: right;
  transition: transform 0.3s;
}
.nav__menu-item a:hover::after,
.nav__menu-item button:hover::after {
  transform: scalex(1);
  transform-origin: left;
}
.nav__menu-more-btn {
  justify-content: center;
  align-items: center;
  width: 5%;
  max-height: 140px;
}
.nav__menu-close-btn {
  position: absolute;
  top: 2rem;
  right: 4rem;
  padding: 1rem;
  background-color: transparent;
  color: rgb(255, 255, 255);
  border: none;
  font-size: 2.4rem;
  cursor: pointer;
  transition: color 0.4s;
}
.nav__menu-close-btn:hover {
  color: rgb(196, 46, 56);
}
.nav__menu-return-btn {
  padding: 1rem;
  position: absolute;
  top: 2rem;
  right: 4rem;
  font-size: 2.6rem;
  color: rgb(255, 255, 255);
  background-color: transparent;
  border: none;
  cursor: pointer;
  opacity: 0;
  pointer-events: none;
  transition: color 0.4s;
}
.nav__menu-return-btn:hover {
  color: rgb(196, 46, 56);
}
.nav__menu-contact-box {
  padding-top: 8rem;
  display: flex;
  justify-content: space-around;
  height: 30%;
}
.nav__menu-contact-box h3 {
  margin-bottom: 1em;
  font-size: 2.4rem;
  text-transform: uppercase;
  color: rgb(196, 46, 56);
}
.nav__menu-contact-box a, .nav__menu-contact-box button {
  display: block;
  margin-bottom: 0.3em;
  font-size: 1.6rem;
  text-transform: uppercase;
  text-decoration: none;
  border: none;
  background-color: transparent;
  color: rgb(152, 152, 154);
  cursor: pointer;
}
.nav__menu-contact-phone h3 {
  font-size: 1.8rem;
  color: rgb(255, 255, 255);
}
.nav__about-us {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  transform: translateX(-200%);
  width: 100%;
  height: 100vh;
  transition: transform 0.4s;
  z-index: -10;
}
.nav__about-us .active {
  transform: translateX(0);
}
.nav__about-us-bgc {
  position: absolute;
  top: 0;
  left: 0;
  max-width: 2200px;
  opacity: 0.6;
}
.nav__about-us-content {
  position: relative;
  margin-top: 20rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  max-width: 992px;
  width: 90%;
}
.nav__about-us-content h3 {
  margin-bottom: 5rem;
  font-size: 3.4rem;
  font-weight: 600;
  text-transform: uppercase;
  color: rgb(255, 255, 255);
}
.nav__about-us-content p {
  font-size: 1.6rem;
  font-family: "Manrope", sans-serif;
  line-height: 1.6;
  text-align: justify;
  color: rgb(255, 255, 255);
  font-weight: 300;
}
.nav__mobile-menu-btns {
  display: none;
}
.nav__mobile-menu-btn {
  display: block;
  padding: 0.4em;
  font-size: 2.6rem;
  background-color: rgb(21, 46, 91);
  color: rgb(255, 255, 255);
  border-radius: 5px;
  border: none;
  transition: background-color 0.4s, color 0.4s;
  cursor: pointer;
}
.nav__mobile-menu-btn:hover {
  background-color: rgb(255, 255, 255);
  color: rgb(0, 0, 0);
}
.nav__more-btns {
  display: flex;
  justify-content: flex-end;
  max-width: 925px;
  width: 100%;
  height: auto;
}
.nav__langBtn-box {
  max-width: 80px;
  width: 100%;
}
.nav .show-btn {
  opacity: 1;
  pointer-events: all;
}
.nav .hide-btn {
  opacity: 0;
  pointer-events: none;
}

