footer {
  background-color: rgb(0, 0, 0);
}

.footer {
  padding: 0 2rem;
  position: relative;
  padding-bottom: 1rem;
  width: 100%;
}
.footer__box {
  margin: 0 auto;
  max-width: 1200px;
}
.footer__brands {
  height: 11rem;
  overflow-x: hidden;
}
.footer__brands h3 {
  margin: 1rem 0 0;
  font-size: 1rem;
  text-transform: uppercase;
  color: rgb(217, 217, 217);
}
.footer__brands-box {
  position: relative;
  display: flex;
  will-change: transform;
  animation: slider-offer 20s linear infinite;
  min-width: 1200px;
}
.footer__brands-box > * {
  flex: 0 0 100%;
}
.footer__list {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-height: 80px;
  transform: translateX(-100%);
}
.footer__list img {
  margin: 0 3.6rem;
  width: 80px;
  height: auto;
}
.footer__foot-link {
  padding: 1rem 0;
  display: flex;
  justify-content: flex-end;
  font-size: 1.2rem;
  color: rgb(255, 255, 255);
  letter-spacing: 0.1rem;
}
.footer__foot-link a {
  font-weight: 800;
  color: #de3d48;
}

@keyframes slider-offer {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(100%);
  }
}
.faq {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgb(0, 0, 0);
  transform: translateX(-120%);
  transition: transform 0.4s;
  z-index: 10;
}
.faq.active {
  transform: translateX(0);
  overflow: scroll;
}
.faq h2,
.faq span,
.faq p {
  font-family: "Roboto Mono", monospace;
}
.faq__box {
  margin: 0 2rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.faq__head {
  margin: 6rem 2rem 2rem;
  display: flex;
  justify-content: space-between;
  max-width: 1800px;
  width: 100%;
}
.faq__head-logo {
  max-width: 200px;
  height: 100%;
}
.faq__head-logo img {
  aspect-ratio: 11/4;
  max-width: 150px;
  width: 100%;
  height: auto;
}
.faq__head-menu button {
  background-color: transparent;
  color: rgb(255, 255, 255);
  transition: color 0.4s;
}
.faq__head-menu button:hover {
  color: rgb(196, 46, 56);
}
.faq__close-btn {
  position: absolute;
  top: 2.7rem;
  right: 4rem;
  padding: 1rem;
  border: none;
  font-size: 2.6rem;
  cursor: pointer;
}
.faq__close-btn svg {
  width: 28px;
  height: 28px;
}
.faq__body {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 1250px;
}
.faq__body-title {
  margin-bottom: 4rem;
  align-self: flex-start;
  max-width: 400px;
  color: rgb(255, 255, 255);
  font-size: 2.4rem;
}
.faq__body-title h2 {
  display: inline-block;
  padding: 1.6rem 0.8rem;
  border: 1px solid rgb(255, 255, 255);
  font-weight: 400;
}
.faq__box-lists {
  padding-bottom: 6rem;
  display: flex;
  justify-content: center;
  gap: 5rem;
  width: 100%;
}
.faq__accordeon-box {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 5rem;
  width: 100%;
}
.faq__accordeon-box span {
  color: rgb(255, 255, 255);
}
.faq__accordeon-box-content {
  margin-top: 1rem;
  display: flex;
  justify-content: space-around;
  width: 100%;
  max-width: 650px;
  cursor: pointer;
}
.faq__accordeon-box-content span {
  margin: auto 0;
  font-size: 1.4rem;
  max-width: 500px;
  width: 100%;
}
.faq__accordeon-box-content i {
  margin-right: 1rem;
  padding: 1.2rem;
  font-size: 2.2rem;
  font-style: normal;
  letter-spacing: -2px;
  color: rgb(196, 46, 56);
  border-radius: 8px;
  background: rgb(36, 36, 36);
  background: linear-gradient(180deg, rgb(36, 36, 36) 20%, rgba(0, 0, 0, 0.8) 93%);
}
.faq__accordeon-btn {
  padding: 0.2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 50px;
  width: 100%;
  font-size: 2.6rem;
  color: rgb(255, 255, 255);
  background-color: transparent;
  border: none;
  transform: rotate(0);
  transition: transform 0.4s, color 0.4s;
  cursor: pointer;
}
.faq .active {
  transform: rotate(45deg);
  color: rgb(196, 46, 56);
}
.faq__accordeon-info {
  display: none;
  position: relative;
  max-width: 450px;
}
.faq__accordeon-info-text {
  margin-bottom: 2em;
  font-size: 1.4rem;
  color: rgb(255, 255, 255);
}
.faq .show-acc {
  display: block;
}

