.contact__attachments-box {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1em;
}
.contact__attachments-btn {
  padding: 1em 2em;
  width: 25%;
  font-size: 1rem;
  color: rgb(255, 255, 255);
  background-color: rgb(21, 46, 91);
  border: none;
  border-radius: 3px;
  cursor: pointer;
  transition: color 0.4s, background-color 0.4s;
}
.contact__attachments-btn:hover {
  background-color: rgb(255, 255, 255);
  color: rgb(0, 0, 0);
}
.contact__attachments-label {
  margin-left: 1em;
  display: flex;
  justify-content: space-between;
  align-self: center;
  flex-grow: 1;
  width: 50%;
  height: auto;
  font-size: 1.2rem;
  color: rgb(255, 255, 255);
}
.contact__attachments-wetransfer {
  display: flex;
  align-self: center;
  padding: 0.6em 1.6em;
  background-color: rgb(255, 255, 255);
  border: 1px solid rgb(255, 255, 255);
  border-radius: 5px;
  filter: invert(0);
  transition: filter 0.4s;
  cursor: pointer;
}
.contact__attachments-wetransfer:hover {
  filter: invert(100%);
  border: 1px solid rgb(0, 0, 0);
}
.contact__attachments-wetransfer img {
  width: 100%;
  height: auto;
  aspect-ratio: 2/1;
}
.contact__attachments-info {
  position: relative;
  min-height: none;
}
.contact__attachments-info.show-items {
  min-height: 100px;
}
.contact__attachments-info-items {
  position: absolute;
  top: 0;
  display: block;
  width: 100%;
  font-size: 1rem;
  color: rgb(255, 255, 255);
  background-color: rgb(0, 0, 0);
  opacity: 0;
  transform: scale(0);
  transition: opacity 0.4s, transform 0.4s;
}
.contact__attachments-info-items.show-items {
  opacity: 1;
  transform: scale(1);
}
.contact__attachments-close-btn, .contact__attachments-info-btn {
  display: block;
  width: 30px;
  height: 30px;
  background-color: transparent;
  color: rgb(255, 255, 255);
  border: none;
  cursor: pointer;
}
.contact__attachments-info-btn {
  opacity: 1;
  transform: scale(1);
}
.contact__attachments-info-btn.show-items {
  opacity: 0;
  transform: scale(0);
}
.contact__form-btns {
  margin: 2em 0;
  display: flex;
  justify-content: center;
  width: 40rem;
}
.contact__form-btn {
  margin: 0 2rem;
  padding: 1rem 4rem;
  font-size: 1rem;
  background-color: rgb(196, 46, 56);
  color: rgb(255, 255, 255);
  border-radius: 3px;
  transition: background-color 0.4s, color 0.4s;
  border: none;
  cursor: pointer;
}
.contact__form-btn--send.error {
  pointer-events: none;
}
.contact__form-btn:hover {
  background-color: rgb(255, 255, 255);
  color: rgb(0, 0, 0);
}
.contact__popup {
  padding: 0 2rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 500px;
  width: 100%;
  font-family: "Roboto Mono", monospace;
  border-radius: 5px;
  z-index: 10;
}
.contact__popup-box.show {
  display: flex;
}
.contact__popup-box {
  padding: 2rem;
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 3em;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.9);
}
.contact__popup-box span {
  font-size: 1.8rem;
  text-transform: uppercase;
  color: rgb(255, 255, 255);
}
.contact__popup-box button {
  padding: 0.6em 2em;
  font-size: 1.2rem;
  color: rgb(255, 255, 255);
  background-color: rgb(21, 46, 91);
  border: none;
  border-radius: 5px;
  transition: color 0.4s, background-color 0.4s;
  cursor: pointer;
}
.contact__popup-box button:hover {
  color: rgb(0, 0, 0);
  background-color: rgb(255, 255, 255);
}
.contact__body-span {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  max-width: 320px;
  width: 100%;
}
.contact__body-span h3 {
  padding: 1.2rem 0.9rem;
  font-size: 1.2rem;
  font-family: "Roboto Mono", monospace;
  font-weight: 600;
  text-transform: uppercase;
  border: 1px solid rgb(255, 255, 255);
  color: rgb(255, 255, 255);
}
.contact__body-span-box {
  margin-top: 15rem;
}
.contact__body-socials {
  margin-right: 6rem;
  display: flex;
  align-self: flex-end;
  flex-wrap: wrap;
}
.contact__socials-box {
  display: flex;
  justify-content: space-around;
  align-items: center;
  border: 1px solid rgb(22, 22, 22);
  border-radius: 5px;
}
.contact__socials-box a {
  padding: 0.3em;
  color: #b60732;
  transition: filter 0.4s, color 0.4s;
}
.contact__socials-box a:hover {
  color: rgb(255, 255, 255);
  filter: invert(100);
}
.contact__socials-box svg {
  width: 30px;
  height: 30px;
  padding: 0.2em;
  border-radius: 5px;
  background: rgb(36, 36, 36);
  background: linear-gradient(180deg, rgb(36, 36, 36) 6%, rgb(0, 0, 0) 78%);
}

.blackBgc {
  background-color: rgb(0, 0, 0);
}

.visible {
  opacity: 1;
}

@keyframes slider-header-f {
  0% {
    transform: translate3d(0, 0, 0);
  }
  100% {
    transform: translate3d(-201%, 0, 0);
  }
}
@keyframes slider-header-s {
  0% {
    transform: translate3d(0, 0, 0);
  }
  100% {
    transform: translate3d(-201%, 0, 0);
  }
}
@keyframes tracking-in-expand-fwd {
  0% {
    letter-spacing: -0.5em;
    transform: translateZ(-700px);
    opacity: 0;
  }
  40% {
    opacity: 0.6;
  }
  100% {
    transform: translateZ(0);
    opacity: 1;
  }
}
